input[type=text] {
  font-size: 20px;
  width: 30vw;
  border: 2px solid #aaa;
  border-radius: 4px;
  margin: 10px;
  outline: none;
  padding: 5px 10px;
  box-sizing: border-box;
  transition: .3s;
}
input[type=text]:focus {
  border-color: #B23121;
  box-shadow: 0 0 8px 0 #D44638;
}


input[type=checkbox]+label {
  display: block;
  margin: 0.2em;
  cursor: pointer;
  padding: 0.2em;
}

input[type=checkbox] {
  display: none;
}

input[type=checkbox]+label:before {
  content: "\2714";
  border: 0.1em solid #000;
  border-radius: 0.2em;
  display: inline-block;
  width: 1em;
  height: 1em;
  padding-left: 0.2em;
  padding-bottom: 0.3em;
  margin-right: 0.2em;
  vertical-align: bottom;
  color: transparent;
  transition: .2s;
}

input[type=checkbox]+label:active:before {
  transform: scale(0);
}

input[type=checkbox]:checked+label:before {
  background-color: #B23121;
  border-color: #D44638;
  color: #fff;
}


/*  */
button.addTask {
  cursor: pointer;
  padding: 4px;
  text-transform : uppercase;
  margin: 20px;
  font-size: 22px;
  transition: all .5s ease-in-out;
  background-color: #B23121;
  border-color: #D44638;
  color: #fff;
}
button.addTask:hover {
  border-color: #ddd;
}
button.addTask:focus {
  animation: buttonClick .5s;
}
@keyframes buttonClick{
  0%{
    transform: scale(1);
  }
  50%{
    transform: scale(1.5);
  }
  100%{
    transform: scale(1);
  }
}

input[type=date]{
  background-color: #B23121;
  border-color: #D44638;
  color:white;
  font-size: 22px;

}
input[type=date]:hover{
  border-color: #ddd;
}
