body{
  background-color: rgb(31, 30, 30);
}
.app {
  text-align: center;
  color:white;
  font-size: 22px;
}
h1{
  text-transform: uppercase;
}


.task{
  background-color: #ddd;
  height: 40px;
  line-height: 40px;
  color:black;
  box-sizing: border-box;
  transition: all .5s ease-in-out;
}
.task:nth-child(2n + 1){
  background-color: white;
  transition: all .1s ease-in-out;
}
.task:hover{
  background-color: rgb(196, 195, 195);
}
.task:nth-child(2n + 1):hover {
  background-color: rgb(196, 195, 195);
}
.completeButton {
  cursor: pointer;
  text-transform: uppercase;
  font-size: 20px;
  height: 30px;
  background-color: #B23121;
  border-color: #D44638;
  color: #fff;
  margin-left: 30px;
  transition: all .5s ease-in-out;
}
.completeButton:hover {
  border-color: #ddd;
}
.xButton{
    transition: all .5s ease-in-out;
    cursor: pointer;
    text-transform : uppercase;
    font-size: 20px;
    height: 30px;
    background-color: #B23121;
    border-color: #D44638;
    color: #fff;
}
.xButton:hover{
  border-color: #ddd;
}
